<template>
    <li class="todo-item">
        Item {{site+1}} :    {{item}}
    
    </li>
</template>

<script>
export default {
    props:['item','site'], //從上層傳來的data參數，可直接用在上方 {{data}}
}
</script>


<style>
    li{
        list-style-type: none;
        margin-top: 30px;
        font-size: 24px;
        border-bottom: 1px solid #333;
        line-height: 45px;
    }
</style>


