<template>
    <div class="categoryPanel">
        
            <div class="container">
				<div class="row">
					<div class="post-title">
						<h2>Backend Post Platform</h2>
					</div>
				</div>
			</div>

            <div class="container">
				<div class="row">
					<div class="col-12">
						<div class="categoryDescribe">
							使用說明:<br>
							下方第一個item為不可編輯或刪除的範例<br>
							請直接Add一個新的item即可做編輯或刪除<br>
						</div>
					</div>	
					<div class="col-12">
						<div class="text-right">
							<a href="##" @click="showTable" class="btn-ray">Add <i class="icon-user-add"></i></a>
						</div>
					</div>
					<table class="col-12 itemList">
						<!-- <thead> -->
							<tr class="text-center">
								<td>地點分類</td>
								<td>行程名稱</td>
								<td>縮圖</td>
								<td>是否免稅</td>
								<td>原價</td>
								<td>特價</td>
								<td>是否啟用</td>
								<td>編輯欄位</td>
							</tr>
						<!-- </thead> -->
			
						<!-- <tbody> -->
							<tr class="text-center">
								<td>東京</td>
								<td>自由行</td>
								<td><img src="https://picsum.photos/100" width="80" height="80"></td>
								<td>true</td>
								<td>15000</td>
								<td>10000</td>
								<td>true</td>
								<td><a href="##" class="btn-ray mg10"><i class="icon-pencil-1"></i></a><a href="##" class="btn-ray"><i class="icon-trash"></i></a></td>
							</tr>
							<tr v-for="(data,index) in realData" class="text-center">
								<td>{{data.category}}</td>
								<td>{{data.title}}</td>
								<td><img :src="data.imageUrl" width="80" height="80"></td>
								<td>{{data.option.tax}}</td>
								<td>{{data.origin_price}}</td>
								<td>{{data.price}}</td>
								<td>{{data.enabled}}</td>
								<td>
									<a href="##" @click="editItem(data)" class="btn-ray mg10"><i class="icon-pencil-1"></i></a>
									<a href="##" @click="deleteItem(data.id)" class="btn-ray"><i class="icon-trash"></i></a>
								</td>
							</tr>
						<!-- </tbody> -->
					</table>
				</div>
			</div>

            <div class="itemTable">
			<div class="container new-item">
				<div class="row">
					<div class="col-12 tableElement">
						<div class="item-title text-center">新增產品</div>
					</div>
					<div class="col-3 tableElement">
						<div class="inputImgUrl">
							<label for="itemImg">輸入圖片網址</label><br>
							<input type="text" id="itemImg" v-model="inputProduct.imageUrl" placeholder="請輸入圖片網址">
							<img :src="inputProduct.imageUrl" class="img-fluid">
						</div>
					</div>
					<div class="col-9">
						<div class="row">
							<div class="col-12 tableElement">
								<label for="itemTitle">行程名稱</label><br>
								<input type="text" id="itemTitle" class="w-100" v-model="inputProduct.title" placeholder="請輸入產品名稱">
							</div>
							<div class="col-6 tableElement">
								<label for="itemSite">地點分類</label><br>
								<input type="text" id="itemSite" class="w-100" v-model="inputProduct.category" placeholder="請輸入地點">
							</div>
							<div class="col-6 tableElement">
								<label>是否免稅</label><br>
								<input type="checkbox" id="itemTax" v-model="inputProduct.option.tax"><label for="itemTax">免稅</label>
							</div>
							<div class="col-6 tableElement">
								<label for="itemOriginPrice">原價</label><br>
								<input type="text" id="itemOriginPrice" class="w-100" v-model="inputProduct.origin_price" placeholder="原始價格">
							</div>
							<div class="col-6 tableElement">
								<label for="itemPrice">特價</label><br>
								<input type="text" id="itemPrice" class="w-100" v-model="inputProduct.price" placeholder="特價價格">
							</div>
							<div class="col-12 tableElement">
								<label for="itemDesc">產品描述</label><br>
								<textarea type="text" id="itemDesc" class="w-100" rows="3" v-model="inputProduct.description" placeholder="請輸入產品描述"></textarea>
								
							</div>
							<div class="col-12 tableElement">
								<label for="itemContent">說明內容</label><br>
								<textarea type="text" id="itemContent" class="w-100" rows="3" v-model="inputProduct.content" placeholder="請輸入說明內容"></textarea>
							</div>
							<div class="col-12 tableElement">
								<input type="checkbox" id="itemEnable" v-model="inputProduct.enabled">
								<label for="itemEnable">是否啟用</label>
							</div>
							
						</div>
						
					</div>
					<div class="col-12 tableElement text-right" v-if="!edit">
						<a href="##" @click="insertData" class="btn-ray mg10">確認</a>
						<a href="##" @click="cancle" class="btn-ray">取消</a>
					</div>
					<div class="col-12 tableElement text-right" v-else>
						<a href="##" @click="editComplete" class="btn-ray mg10">更新</a>
						<a href="##" @click="cancle" class="btn-ray">取消</a>
					</div>
				</div>
				

			</div>
		</div>
    </div>
    


    
</template>


<script>
export default {
    data(){
        return{
            edit:false,
            nowSite:0,
            show:false,
            initialProduct:{
                title:'',
                category:'',
                content:'',
                description:'',
                imageUrl:'',
                enabled:false,
                origin_price:null,
                price:null,
                unit:'',
                option:{
                    tax:false,
                },
            },
            inputProduct:{
                title:'',
                category:'',
                content:'',
                description:'',
                imageUrl:'',
                enabled:false,
                origin_price:null,
                price:null,
                unit:'',
                option:{
                    tax:false,
                },
            },
            tempData:{},
            realData:[],
        }
    },
    methods:{
            showTable(){
                this.show=true;
                if(this.show){
                    document.querySelector(".itemTable").classList.add("show");
                }
            },
            cancelTable(){
                this.show=false;
                document.querySelector(".itemTable").classList.remove("show");
            },
            insertData(){
                console.log(this.inputProduct);
                
                let newData=this.inputProduct;
                let time=(new Date()).getTime();

                this.$set(newData,'id',time);
                console.log(newData);
                this.realData.push(newData);
                console.log(this.realData);

                this.clearTable();
            },
            clearTable(){
                this.inputProduct=this.initialProduct;
                this.cancelTable();
            },
            editItem(data){
                this.showTable();

                this.edit=true;
                var i;
                this.tempData=JSON.parse(JSON.stringify(data));
                console.log(this.tempData);
                this.realData.forEach(function(element,index){
                    // console.log(element);
                    if(element.id===data.id){
                        console.log(`find it!! ${element.id}`);
                        i=index;
                    }
                })

                this.inputProduct=JSON.parse(JSON.stringify(this.realData[i])); 
                //Ray:這邊一定要放深層複製,否則會改到原本的realData。

                this.nowSite=i;
                
            },
            editComplete(){
                console.log(this.nowSite);

                this.realData[this.nowSite]=this.inputProduct;
                
                this.edit=false;
                this.clearTable();
            },
            deleteItem(id){
                var deleteSite;
                this.realData.forEach(function(element,index){
                    if(id===element.id){
                        deleteSite=index;
                    }
                })

                this.realData.splice(deleteSite,1);
            },
            cancle(){
                this.edit=false;
                this.clearTable();
            }
       }
}
</script>

<style scoped>

section{
	margin-top: 60px;
}

section:first-child{
	margin-top: 40px;
}

ul{
	margin:0;
	padding:0;
}

li{
	list-style-type: none;
}

a:hover{
	text-decoration: none;
}

/*About is default set for css*/

table{
	line-height: 120px;
}

tr:first-child{
	border-bottom: 2px solid #ffcdd2;
}

input{
	margin-top: 10px;
	padding:2px 5px;
	outline: none;
	border-width: 0px;
}

input:focus{
	border-width: 1px;
	
}

textarea{
	margin-top: 10px;
	padding:2px 5px;
	outline: none;
	border-width: 0px;
}

textarea:focus{
	border-width: 1px;
}

.btn-ray{
	padding:10px;
	color: #eee;
	background-color: #e57373;
	border-radius: 5px;
	transition: 0.3s all;
}

.btn-ray:hover{
	color: #d6b161;
	background-color:#333;
	
}

.mg10{
	margin:0 10px;
}

.itemList{
	margin-top: 50px;
}


.post-title{
	background-color: #4CAF50;
    padding: 13px;
    color: #eee;
    font-style: oblique;
}

.item-title{
	padding-bottom: 10px;
    border-bottom: 1px solid #666;
}

.tableElement{
	margin-top: 10px;
}


.new-item{
	width: 800px;
	border: 2px solid #333;
	padding:25px;
	background-color: #FFF8E1;
	border-radius: 20px;
}


.itemTable{
	display: none;

	position: fixed;
    top: 30px;
    left: 0;
    right: 0;
}

.itemTable.show{
	display: flex;
}

.categoryPanel{
    margin-bottom: 100px;
}

.categoryDescribe{
	padding:20px;
	/* background-color: rgba(0,0,0,0.5); */
	border: 1px solid #333;

	margin:30px 0;
	width:350px;
}




</style>


