<template>
    <div class="container pushMgn todoPanel">
        <div class="row">
            <div class="col-12 text-center">
                <div class="todoTitle">
                    <h2>Todo list</h2>
                </div>
                <TodoInput @submit="addItem" class="todoInput"/>
                <div class="todoItems">
                    <div class="todoItemsTitle">
                        待辦清單
                    </div>
                    <ul>
                        <TodoItem v-for="(todo,index) in todos" :item="todo" :site="index" @click.native="removeItem(index)"/>
                    </ul>
                </div>
                
            </div>
        </div>
        
    </div>
</template>


<script>

import TodoInput from './TodoInput';
import TodoItem from './TodoItem';

export default {
    data(){
        return{
            todos:[]
        }
    },
    components:{
        TodoInput,
        TodoItem,
    },
    methods: {
        addItem(item){
            this.todos.push(item);
        },
        removeItem(index){
            let isDelete=confirm("Do you really want to delete the item ?");
            if(isDelete){
                this.todos.splice(index,1);
            }
        }
       
    },
}
</script>


<style>
    .pushMgn{
        margin-top: 50px;
        margin-bottom: 100px;
    }

    ul{
        text-align: left;
    }

    .todoPanel{
        padding:50px;
        border:1px solid #333;
        border-radius: 30px;
        background-image: url('https://picsum.photos/1920/1080');
        background-position: 50%;
        background-repeat: no-repeat;
        color: #333;
    }

    .todoTitle{
        padding:10px 20px;
        background-color: rgba(256,256,256,0.5);
        width: 300px;
        margin:0 auto;
        border-radius: 20px;
    }



    .todoItems{
        background-color: rgba(256,256,256,0.5);
        width: 500px;
        margin:0 auto;
        margin-top: 50px;
        padding:50px;
    }

    .todoItemsTitle{
        font-size: 24px;
        letter-spacing: 1px;
    }

    .todoInput{
        margin-top: 50px;
    }
</style>


