<template>
    <form @submit.prevent="submit"> <!--擋掉原先的submit並執行methods的submit-->
        <input v-model="text"/>
        <button type="submit" class="btn btn-primary" >Submit</button> <!--這邊記得type要設submit-->
    </form>

</template>


<script>
export default {
    data(){
        return {
            text:'',
        }
    },
    methods: {
        submit(){
            if(this.text==""){
                alert('The input can not empty !');
                return;
            }
            this.$emit('submit',this.text); //透過$emit定義一個發出submit事件
            this.text='';
        }
    },
}
</script>

<style scoped>
    input{
        width: 250px;
        padding: 5px 10px;
        margin-right: 20px;
    }
    
</style>




