<template>
    <div class="">
        <Menu/>

        <div>
          <img src="https://picsum.photos/1920/500" alt="" class="img-fluid">
        </div>

        <div class="col-12 sideTitle text-center">
            Welcome to my Vue's SPA side Project!!
        </div>
        <router-view/>

        <Footer/>
    </div>
</template>


<script>


import Vue from 'vue';
import VueRouter from 'vue-router';

import Menu from './Menu';
import Products from './Products';
import About from './About';
import Video from './Video'
import Cursor from './Cursor'
import Todo from './Todo';
import Footer from './Footer';
import Category from './Category';
import Igapi from './Igapi';

Vue.use(VueRouter);  //使用VueRouter

export default {
    components:{
      Menu,
      Footer,
    },
    router:new VueRouter({
      routes:[
         {path:'/about',
         component:About,
         children:[
            {path:'video',component:Video},
            {path:'cursor',component:Cursor}
         ]},
         {path:'/products',component:Products},
         {path:'/todo',component:Todo},
         {path:'/category',component:Category},
         {path:'/igapi',component:Igapi},
      ],
    }),
}

</script>

<style>
  .sideTitle{
    font-size: 40px;
    letter-spacing: 2px;
    margin-top:100px;
    margin-bottom: 100px;
  }
</style>


