<template>
    <div @click="imgRemove">
       <div class="container">
			<div class="row">
				<div class="col-12 text-center imgTitle">
					<div>
						<h2>IG tag search</h2>
					</div>
					<div class="inputPanel">
						<input type="text" v-model="searchItem" @keyup.enter="search">
						<a href="#" @click="search">Search</a>
					</div>

					<div v-if="isLoading" class="loadingShow">
						Please wait for search.
					</div>
				</div>
			</div>
			<div class="row imgPanel">
				<div v-for="(data,index) in pictures" class="col-2 imgPicture">
					<img :src="data" alt="" class="img-fluid" @click.stop="imgClick(index)">
				</div>
			</div>
		</div>

		<div class="imgShow">
			<img :src="showImg" alt="none">
		</div>

    </div>
</template>


<script>
export default {
    data(){
        return{
            searchItem:"",
			baseUrl:"https://www.instagram.com/explore/tags/",
			realUrl:"",
			pictures:[],
			showImg:"",
            ifShow:false,
            isLoading:false,
        };
    },
    methods: {
        search(){
            this.isLoading=true;

            this.emptyPicture();

            console.log("yo",this.searchItem);
            this.realUrl=`${this.baseUrl}${encodeURI(this.searchItem)}/?__a=1`;
            console.log(this.realUrl);

            axios.get(this.realUrl)
                .then((res)=>{

                    // console.log(this); //由於用箭頭函式,所以仍指在Vue組件內

                    let pageInfo=res.data.graphql.hashtag.edge_hashtag_to_media.edges;
                    
                    // console.log(pageInfo[0].node.display_url);

                    pageInfo.forEach(element => {
                        this.pictures.push(element.node.thumbnail_src);
                    });

                    // console.log(this.pictures);
                    this.isLoading=false;
                    
                }).catch((res)=>{
                    this.isLoading=false;
                    console.log(res);
                    alert("搜尋不到");
                })
        },
        emptyPicture(){
            this.pictures=[];
        },
        imgClick(index){
                //注意!!! 在imgClick的時候 要阻止冒泡事件!!!

                console.log(index);
                console.log(this.pictures[index]);

                document.querySelector(".imgShow").classList.add("show");
                this.showImg=this.pictures[index];
                this.ifShow=true;
                console.log(this.ifShow);
            
        },
        imgRemove(){
            if(this.ifShow){
                //這邊直接將事件綁在#app上面
                document.querySelector(".imgShow").classList.remove("show");
            }
        },		
	},
}
</script>


<style scoped>

html {
    height: 100%;
}

body {
    background-image: url('https://picsum.photos/1920/1080');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
}

.imgTitle{
    margin:30px 0;
    padding: 50px;
    background-color: rgba(0,0,0,0.5);
    border-radius: 30px;
}

.imgTitle input{
    border: none;
    padding: 5px 10px;
    outline: none;
}

.imgTitle h2{
    color:#eee;
}

.imgShow{
    display:none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    background-color:rgba(0,0,0,0.5);
    
}

.imgShow.show{
    display: flex;
}

.imgShow img{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%, 0);
    width: 400px;
    height: 400px;
}

.imgPanel{
    margin-top: 50px;
}

.imgPicture{
    margin:30px 0;
}

.inputPanel{
    margin-top: 20px;
}

.inputPanel a{
    color: #eee;
    margin-left: 20px;
    padding: 10px;
    font-size: 20px;
    text-decoration: none;
    background-color: #d6b161;
    border-radius: 10px;
    transition: 0.3s all;
}

.inputPanel a:hover{
    color:#d6b161;
    background-color: #333;
}


.loadingShow{
    margin-top:20px;
    color:#eee;
    font-size:30px;
    opacity: 0;
    animation-name: loading;
    animation-duration: 3s;
    animation-iteration-count:infinite;
    
}

@keyframes loading{
    0%{
        opacity: 0;
    }

    50%{
        opacity: 50%;
    }

    100%{
        opacity: 0;
    }
}


</style>


